import{ mapState,mapMutations} from 'vuex'
export default {
	data() {
		return {

			active:1,
			currentIndex:0,
			bannerList:[],
			tabs: [],
			listsTab:[],
			
			
			
			pageNum:1,//当前页数
			pageSize:10,//每页条数
				
		};
	},
	onShareAppMessage(res) {
	      return {
	        title: '我在行动打卡，快来围观我吧',
	        path: '/pages/index/index'
	      }
	    },
	
	onLoad() {
	   this.indexData()
	},
	 computed: {  
	            ...mapState(['hasLogin'])  
	        },  
	methods:{
		...mapMutations(['logIn','logOut'])  ,
		goPage(url){
			uni.navigateTo({
				url
			});
		},
		//首页获取label和根据时间获取列表
		indexData:function(){
			let token='';
			try{
			   token=uni.getStorageSync('token');
											  
			}catch(e){
					console.log(Error)
			};
			console.log(this.hasLogin)
			console.log(this.token)
			
			if(!this.hasLogin &&token==''){
				   this.logOut();
				   uni.navigateTo({
					url:"../login/login",
				   });
				   return false
							
			}else{
				   this.xd_request_post(this.xdServerUrls.xd_bannerList,{},false
			        ).then((res) => {
									   this.bannerList=res.obj
								 
									  
								   }).catch(err => {
						
								});
								this.xd_request_post(this.xdServerUrls.xd_label,{},false
									   ).then((res) => {
										   console.log(res)
										   this.tabs=res.obj
										   
									   }).catch(err => {
									
								});
								this.xd_request_post(this.xdServerUrls.xd_pushByHighGradeList,
								{
									token:token,
									pageNum:1,
									pageSize:10,
								},
								false
									   ).then((res) => {
										   this.listsTab=res.obj.list;
										   this.pageNum=res.obj.nextPage;
									   }).catch(err => {
							
		                                                   });
		  }
			
		},
		//点赞
		loveClick(list,index,boxIndex){
			let isLove=list.islove;
			this.xd_request_post(this.xdServerUrls.xd_saveGiveLikeByPush,{
				pushId:list.id,
				initiatorUserId:uni.getStorageSync('id'),
				giveLikeUserId:list.userId,
			},false
			   ).then(res => {
					   console.log(res)
					   if(isLove==1){								   
											this.listsTab[boxIndex].lists[index].islove=0;
											this.listsTab[boxIndex].lists[index].love--;
					   }else if(isLove==0){
											  this.listsTab[boxIndex].lists[index].islove=1;
											  this.listsTab[boxIndex].lists[index].love++;
					                         						  
					    }
				   }).catch(err => {
				
			})
			
			
		},
		// 最新
		showNew: function () {
			this.active = 0;
		},
		// 推荐
		showRecommend : function(){
			this.active = 1;
		},
		// 关注
		showFollow : function(){
			this.active = 2;
		},
		
		// 推荐内容切换
		navChange: function (e) {
			this.currentIndex = e.currentTarget.dataset.index;
		},
		getNewList(){
			this.indexData(),
			this.pageNum=1,
			uni.stopPullDownRefresh();
		},
		 getReachList(){
			if(this.pageNum==0){
				uni.showLoading(
				{
					title: '没有更多数据了'
				})
				setTimeout(function () {
				    uni.hideLoading();
				}, 1000);
				
				return false
			}
			uni.showLoading(
			{
				title: '加载中..',
			})
			this.xd_request_post(this.xdServerUrls.xd_pushByHighGradeList,
			{
				token:uni.getStorageSync('token'),
				pageNum:this.pageNum,
				pageSize:this.pageSize,
			},
			false
				   ).then(res=>{
					   this.pageNum=res.obj.nextPage;
					
					this.listsTab = this.listsTab.concat(res.obj.list);					
					setTimeout(function () {
					    uni.hideLoading()
					}, 2000);
					
				})
		}
		
	},
	onShow() {
		this.indexData()
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.getNewList()
	},
	// 上拉加载
	onReachBottom() {
		this.getReachList()
	}
	
	
}

